:root {
  --quick-creator-background: var(--link-text);
  --quick-creator-text: var(--label-default-text);
}

.content-wrapper {
  margin-left: $sidebarWidth;
}

.main-sidebar {
    width: 280px;
    background-color: var(--sidebar-background);
    color: var(--sidebar-text);

    background-size: 280px 210px;
    background-repeat: no-repeat;

    position: absolute;
    top: 0;
    left: 0;
    padding-top: 50px;
    min-height: 100%;
    transition: transform 0.3s ease-in-out, width 0.3s ease-in-out;

    a {
        color: var(--sidebar-link);
    }

    .sidebar-menu {
        background-color: unset;
        margin-bottom: 60px;
        list-style: none;
        margin: 0;
        padding: 0;
        overflow: hidden;
        white-space: nowrap;

        .sidebar-icon-link {
            color: #474747;
            font-size: 16px;

            .fas, .fa, .far, .fab, .ra, .fa-solid, .fa-regular, .fa-brands, .fa-light, .fa-thin, .fa-duotone {
                width: 20px;
            }

            &:hover {
                color: #C2C2C2;
                background: unset;
            }
        }

        li.sidebar-section {
            .sidebar-text {
                padding: 6px 5px 6px 15px;
            }
        }

        li {
            .fa, .ra, .fas, .far, .fab, .fa-solid, .fa-regular, .fa-brands, .fa-light, .fa-thin, .fa-duotone {
                font-size: 14px;
                width: 22px;
            }

            padding: 0 12px;
            min-height: 38px;

            &:hover {
                a {
                    background: unset;
                }
            }

            a, span {
                display: block;
                font-size: 11px;
                line-height: 22px;
                color: var(--sidebar-link);
                letter-spacing: 1.5px;
                text-transform: uppercase;
            }

            a {
                border-left: unset;
                overflow: hidden;

                &:hover {
                    background: rgba(0, 0, 0, 0.16);
                    border-radius: 8px;
                }
            }

            ul.sidebar-submenu {
                list-style-type: none;
                margin: 0;
                padding: 0;

                li {
                    padding-left: 12px;
                    margin: 1px 0;
                }
            }
        }

        li.active > a, li.active.sidebar-section {
            border: none;
            background: rgba(0, 0, 0, 0.24);
            box-shadow: inset 1px 2px 6px rgba(0, 0, 0, 0.16);
            border-radius: 8px;
        }


        .quick-creator-element {
            text-align: center;
            margin-bottom: 0.25rem;

            .quick-creator-button {
                display: flex;
                align-items: baseline;
                justify-content: center;
                font-size: 0.9rem;
                padding: 0.5rem 0;
                width: 70%;
                cursor: pointer;
                text-align: center;
                color: var(--quick-creator-button-text);
                mix-blend-mode: normal;
                background-color: var(--quick-creator-button-background);
                text-transform: uppercase;
                border-radius: 5px;

                i {
                    font-size: 1rem;
                }

                &:hover {
                    transition: all 0.5s ease-in-out;
                    background-color: var(--quick-creator-button-hover-background);
                }
            }
        }
    }
}
.main-sidebar-placeholder {
background-image: url(https://images.kanka.io/app/L5nSYCLgwtxR3wlUGk16fMZ0zAU=/280x210/src/images%2Fbackgrounds%2Fmountain-background-medium.jpg);
  z-index: 820;
}
.sidebar-menu {
    .fa, .fas, .fab, .far, .ra, .fa-solid, .fa-regular, .fa-brands, .fa-light, .fa-thin, .fa-duotone {
        display: inline-block;
        width: 20px;
    }

    li > .treeview-menu {
        background-color: unset;
    }

    li > a, li > span {
        padding: 8px 5px 8px 15px;
    }
}

section.sidebar-campaign {
    height: 160px;
    background: linear-gradient(180deg, rgba(51, 51, 51, 0) 0%, var(--sidebar-background) 100%);
    overflow: hidden;

    .campaign-block {
        text-align: left;
        height: 120px;
        left: 60px;
        padding: 0 24px;
        padding-top: 96px;

        .campaign-head {
            font-size: 20px;
            line-height: 24px;
            letter-spacing: 0.15px;
            height: 24px;
            word-break: break-all;

            .campaign-name {
                overflow: hidden;
                display: block;
                max-height: 24px;
            }

            .campaign-updated {
                font-size: 12px;
                line-height: 16px;
                /* identical to box height, or 133% */

                letter-spacing: 0.4px;

                /* Soft grey */

                color: #C2C2C2;
                mix-blend-mode: normal;
            }
        }
    }
}

