.sidebar-creator {
    position: fixed;
    width: $sidebarWidth;
    height: 48px;
    bottom: 0;
    left: 0;
    background-color: $neutralColour;
    transform: translate(0, 0);
    z-index: 825;

    display: flex;
    flex-direction: row;
    justify-content: center;
    transition: transform 0.3s ease-in-out, width 0.3s ease-in-out;

    a {
        text-align: center;
        color: #fff;
        mix-blend-mode: normal;

        // Whole button clickable
        width: 100%;
        //padding: 14px 0;

        flex: none;
        order: 1;
        align-self: center;
        //margin: 8px 0px;

        line-height: 16px;
        font-size: 14px;
        letter-spacing: 0.75px;
        text-transform: uppercase;

        span {
            transition: transform 0.3s ease-in-out, width 0.3s ease-in-out;
        }
    }
}

body .main-sidebar, body .left-side {
  background-color: var(--sidebar-background);
}
.main-sidebar .sidebar li {
  .fa, .fas, .fab, .far, .ra, .fa-solid, .fa-regular, .fa-brands, .fa-light, .fa-thin, .fa-duotone {
    font-size: 14px;
    vertical-align: text-bottom;
  }
}

.main-footer, .navbar {
  margin-left: 280px;
}



/** Mobile **/
@media (max-width: 767px) {
    .main-sidebar {
        transform: translate(-280px, 0);
    }
    .sidebar-creator {
        transform: translate(-280px, 0);
    }
    .content-wrapper, .main-footer {
        margin-left: 0;
    }


    .sidebar-open .main-sidebar {
        transform: translate(0);
    }
    .sidebar-open .content-wrapper, .sidebar-open .main-footer {
        transform: translate($sidebarWidth, 0);
    }
    .sidebar-open .sidebar-creator {
        transform: translate(0, 0);
    }

    .campaign-switcher {
        left: 10px;
    }
}
@media (min-width: 768px) {
  .sidebar-collapse .main-sidebar {
    transform: translate(0, 0);
    width: 0 !important;
    z-index: 850;
  }
  .sidebar-collapse .sidebar-creator {
    width: 50px !important;
    span {
      display: none;
    }
  }
  .sidebar-collapse .content-wrapper, .sidebar-collapse .right-side, .sidebar-collapse .main-footer {
    margin-left: 0 !important;
    z-index: 840;
  }

  .sidebar-collapse .sidebar-menu > li > .sidebar-text > span, .sidebar-collapse .campaign-updated, .sidebar-collapse .campaign-name {
    display: none !important;
  }
}
